.navbar {
    color: rgb(34, 14, 14);
    background-color: rgb(240, 240, 240);
    display: inline-block;
    width: -webkit-fill-available;
    font-family: 'Maven Pro', sans-serif;
    padding: 0;
}

.navbar-links ul {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-evenly;
}

.navbar-links li {
    list-style: none;
}

.navbar-links a {
    text-decoration: none;
    color: rgb(0, 0, 0);
    padding: 1rem;
    display: block;
}

.navbar-links a:hover {
    color: rgb(255, 255, 255);
    background-color: black ;
}

.toggle-button {
    position: absolute;
    display: none;
    width: 30px;
    height: 21px;
    top: 0.75rem;
    right: 1rem;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
}

.toggle-button .bar {
    height: 3px;
    width: 100%;
    background-color: rgb(0, 0, 0);
    border-radius: 10px;
}

.nav-title {
    display: none;
}

@media (max-width: 700px) {

    body {
        margin: 0;
    }

    .nav-title {
        display: flex;
        /* padding-left: 10px; */
        margin: 0;
        padding: 10px;
    }

    .navbar {
        display: block;
    }

    .navbar-links {
        display: none;
        padding: 0;
    }

    .navbar-links ul {
        display: block;
        width: 100%;
    }

    .navbar-links ul li {
        text-align: center;
    }

    .toggle-button {
        display: flex;
    }
}
.banner, .projects, .contact {
    font-family: 'Comfortaa', cursive;
    font-weight: 300;
    text-align: left;
    margin-top: 100px;
    font-size: 1vw;
}

.banner a, .project-box p > a {
    text-decoration: underline;
    color: black;
}

.banner a:hover, .project-box p > a:hover {
    background-color: black;
    color: white;
    text-decoration: none;
}

.name-desc {
    display: flex;
    flex-direction: column;
    padding-right: 10px;
    width: 50%;
}

.banner-head {
    margin-bottom: 3vw;
    display: flex;
}

.banner-title {
    font-size: 1.5vw;
    font-weight: 400;
}

.banner-head p, .skills > p, .projects > p, .contact > p {
    font-size: 3vw;
    display: inline-block;
    margin: auto 0;
}

.banner-head img {
    width: 10vw;
    height: 10vw;
    border-radius: 5%;
    -webkit-filter: grayscale(1);
            filter: grayscale(1);
}

.banner-head img:hover {
    -webkit-filter: none;
            filter: none;
    transition: 500ms;
}

.banner-details-card, .skills-card {
    width: 50%;
}

.banner-details-card hr, .skills hr, .projects hr, .contact hr {
    width: 10vw;
    margin-top: 0%;
}

.banner-details-card p {
    margin-bottom: 0%;
}

.banner-card-topic {
    font-size: 1.25vw;
}

.banner-card-topic-date {
    font-size: 0.75vw;
}

.banner-card-topic-desc {
    font-size: 1vw;
}

.edu-n-work {
    display: flex;
}

@media (max-width: 700px) {

    .banner {
        margin: 2vw;
    }

    .banner-head {
        flex-direction: column;
        align-items: center;
    }

    .banner-head p, .skills > p, .projects > p, .contact > p {
        width: auto;
        font-size: 10vw;
        text-align: center;
    }

    .banner-head img {
        width: 35vw;
        height: 35vw;
    }

    .edu-n-work, .skills-card-parent, .project-box {
        display: block;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }

    .banner-details-card, .skills-card {
        width: auto;
        text-align: center;
    }

    .banner-details-card ul {
        list-style: none;
        padding: 0;
    }

    hr {
        display: inline-block;
    }

    .banner-title{
        font-size: 8vw;
    }

    .banner-card-topic, .project-box h1 {
        font-size: 5vw;
    }

    .banner-card-topic-date, .project-box > p {
        font-size: 3vw;
    }

    .banner-card-topic-desc, .project-box p {
        font-size: 4vw;
    }

    .name-desc span {
        font-size: 3vw;
        text-align: center;
    }
}
.skills {
    display: flex;
    font-family: 'Comfortaa', cursive;
    margin: 10vh 0;
    flex-direction: column;
}

.skills p {
    display: inline-block;
    margin: 0 auto;
}

.skills hr {
    display: inline-block;
    margin: 0 auto;
}

.skills-card {
    display: inline-flex;
    flex-direction: column;
}

.skills-card-parent {
    margin-top: 5vh;
    display: flow-root;
}

.skill-items {
    display: flow-root;
    text-align: center;
    justify-content: center;
}

.skill-items img {
    width: 125px;
    margin: 10px;
    background: rgb(255, 255, 255);
    border: 1px solid transparent;
    padding: 1vw;
}

.skill-items img:hover {
    border-width: 1px;
    border-style: groove;
    border-color: black;
    transition: 0.5s;
}

.skills-card:last-child:nth-child(odd) {
    margin-left: 25%;
}

.skills-title {
    font-size: 1.5vw;
}

@media (max-width: 700px) {
    .skill-items {
        display: flex;
        flex-direction: column;
    }

    .skill-items img {
        display: inline-block;
        margin: 0 auto;
        height: 10vh;
    }

    .skills-card-parent {
        display: flex;
    }

    .skills-title {
        font-size: 5vw;
    }

    .skills-card {
        margin-bottom: 10vh;
    }

    .skills-card:last-child:nth-child(odd) {
        margin-left: 0%;
    }
}
footer {
    text-align: center;
    background-color:  rgb(240, 240, 240);
}

footer p{
    margin: 0;
    padding: 20px;
    font-family: 'Maven Pro', sans-serif;
}
.projects {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

.project-box {
    margin: 20px 25%;

}

.projects-link {
    display: flex;
}

.projects-link p > a {
    margin-right: 30px;
    margin-top: 20px;
    padding: 2px;
}

.projects-link a img {
    width: 25px;
    padding: 2px;
    border: 1px solid transparent;
}

.projects-link a img:hover {
    border-width: 1px;
    border-style: groove;
    border-color: black;
    transition: 0.5s;
}

@media (max-width: 700px) {
    .project-box {
        margin: 20px 10%;
    }

    .projects {
        margin-top: 20px;
    }

    .projects-link {
        display: flex;
        justify-content: center;
    }
}
.contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

.contact-icon {
    display: contents;
}

.contact-icon a {
    color: black;
    margin: 20px 0;
}

.contact-icon a:hover {
    background-color: black;
    color: white;
    text-decoration: none;
}

@media (max-width: 700px) {
    .contact {
        margin-top: 20px;
    }

    .contact-icon a {
        font-size: 5vw;
    }
}
